hr.vertical { // TODO: finish
    width: 1px;
    height: 100%;
    border: none;
    margin: 8px;
    box-sizing: border-box;
    outline: 0; }

.badge {
    text-align: center;
    font-size: 12px;
    font-family: Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: 600;
    box-sizing: border-box;
    line-height: 16px;
    width: 16px;
    height: 16px;
    border-radius: 8px;
    background: $ping-color;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff; }

.avatar {
    width: 32px; //
    height: 32px; //
    aspect-ratio: 1;
    position: relative;
    border-radius: 50%;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover; }
    * {
        max-width: 100%;
        max-height: 100%; } }


#top-bar {
    width: 100%;
    height: #{$top-bar-height};
    * {
        max-height: #{$top-bar-height}; } }


#main-wrapper {
    height: calc(100vh - #{$top-bar-height}); //
    display: flex; }


#guilds {
    width: $server-list-width;
    height: 100%;
    position: relative;
    overflow: hidden;

    > .scroller {
        height: 100%;
        width: 100%;
        overflow-y: scroll;
        overflow-x: hidden;
        // TODO: options for user to put a scroller of their liking (cannot be larger than the margin in server icons)
        scrollbar-width: none; } //

    #home-btn {
        margin-top: 4px; // web is 12px
        > svg svg {
            padding-top: 14px; } }

    li, .item {
        $icon-size: 48px; // width & height
        $icon-spacing: 8px;

        $border-rad: 50%;
        $border-rad-hov: 17.5px;
        $border-rad-active: $border-rad-hov;

        $side-margins: calc((#{$server-list-width} - #{$icon-size}) / 2);

        width: $icon-size;
        height: $icon-size;
        aspect-ratio: 1;
        position: relative;
        margin: 0 auto $icon-spacing auto; // side margins should be auto /* $side-margins */
        list-style-type: none;
        cursor: pointer;
        border-radius: $border-rad;
        transition: border-radius 0.2s ease-out; // 0.15s
        // TODO: get accurate time for border-radius

        > svg {
            width: $icon-size;
            aspect-ratio: 1;
            border-radius: inherit;
            overflow: hidden;
            transition: background-color 0.15s ease-out, color 0.15s ease-out;
            .wrapper {
                width: 100%;
                height: 100%; }
            svg {
                display: block;
                margin-left: auto;
                margin-right: auto; } }

        &::before { // pill
            --left: calc(-1 * (#{$server-list-width} - #{$icon-size}) / 2);
            content: '';
            background-color: #fff;
            @include abs-pos(50%, calc(var(--left) - 8px), auto, auto);
            transform: translateY(-50%);
            width: 8px; //
            height: 8px; // unreads: 8px, hover: 20px, active: 40px
            border-radius: 0 4px 4px 0;
            transition: height 0.2s, left 0.1s; } // TODO: get accurate time
        &:hover {
            border-radius: $border-rad-hov;
            &::before {
                height: 20px;
                left: calc(var(--left) - 4px); } }
        &.unreads::before, &.unread::before {
            left: calc(var(--left) - 4px); }
        &:active, &.active {
            border-radius: $border-rad-active;
            &::before {
                height: 40px;
                left: calc(var(--left) - 4px); } }
        &:active {
            top: 1px;
            &::before {
                top: calc(50% - 1px); } }

        &.guild-actions {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #3ba55d;
            &:hover, &:active, &.active {
                color: #fff;
                background-color: #3ba55d; } }

        &.no-img .wrapper { // * (childWrapper-anI2G9 acronym-2mOFsV) inside svg foreignObject when Compiling
            font: 12px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-weight: 500;
            line-height: 1.2em;
            white-space: nowrap;
            display: flex;
            align-items: center;
            justify-content: center; }

        .badge {
            @include abs-pos(auto, auto, 50%, 0);
            pointer-events: none;
            // padding-right: 1px
            &.upper {
                top: 0;
                bottom: auto;
                background: #3ba55d; }
            &.lower {
                top: auto;
                bottom: 0;
                background: $ping-color; } } }

    hr, .divider {
        margin: 0 auto 8px auto;
        border: none;
        width: 32px; //
        height: 2px;
        border-radius: 1px;
        background-color: #ffffff0f; }


    $new-width: 56px;
    $new-height: 24px;
    $new-margin: 8px;
    &::before, &::after {
        @include abs-pos(-#{$new-height + $new-margin}, 0, auto, 0);
        // width: 3.5rem //
        // height: 1.5rem //
        max-height: $new-height; //
        padding: 0.281rem 0 0.281rem 0;
        margin: $new-margin;
        border-radius: 50px;
        z-index: 2;

        content: 'NEW';
        opacity: 0.9;
        background: $ping-color;
        color: #fff; //
        font: 0.75rem Ginto, "Helvetica Neue", Helvetica, sans-serif; // ginto is proprietarial
        text-align: center;

        font-weight: 600;
        cursor: pointer;
        // ! Vertically center using css method instead of padding

        --scale: 1; // * make user choose wether they want it to grow or to move // 0
        transition: transform 0.05s, top 0.05s, bottom 0.05s;
        transform: scale(var(--scale, 1));
        transform-origin: top center;

        &:hover {
            opacity: 1;
            background: yellow; } }
    &::after {
        top: auto;
        bottom: -#{$new-height + $new-margin};
        transform-origin: bottom center; }
    &.new-above::before {
        --scale: 1;
        top: 0; }
    &.new-below::after {
        --scale: 1;
        bottom: 0; } }

button {
    background: transparent;
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    box-sizing: border-box; }


#app-base {
    height: calc(100vh - #{$top-bar-height});
    width: calc(100vw - #{$server-list-width});
    display: flex;

    $head-height: 48px; //
    $media-ctrl-height: 52px; //

    &::before {
        content: '';
        height: 1px;
        z-index: 3;
        display: block;
        @include abs-pos(calc(#{$head-height} + #{$top-bar-height} - 1px), $server-list-width, auto, 0);
        pointer-events: none;
        box-shadow: 0 1px 0 #04040533, 0 1.5px 0 #0606070d, 0 2px 0 #0404050d; }

    #sidebar {
        width: $sidebar-width;
        height: 100%;
        border-radius: 8px 0 0 0; //
        overflow-y: hidden;
        display: flex;
        flex-direction: column;

        .head {
            width: 100%;
            height: $head-height;
            display: flex;
            flex: 0 0 auto;
            align-items: center;
            justify-content: center;

            #search-dms {
                width: 220px; //
                height: 28px; //
                border: none;
                border-radius: 4px; //
                padding: 1px 6px;
                font: 14px Whitney, "Helvetica Neue", Helvetica, sans-serif;
                font-weight: 500;
                line-height: 24px;
                text-align: left; //
                cursor: pointer; } }

        .channels {
            width: 100%;
            // height: calc(100% - #{$head-height} - #{$media-ctrl-height})
            flex: 1 1 auto;
            position: relative;
            overflow-y: hidden;
            user-select: none;

            $btn-side-margin: 8px; //
            $btn-vertical-margin: 1px; //
            $btn-height: 42px; //

            .scroller {
                height: 100%;
                padding-top: 8px; //
                padding-right: 8px;
                position: relative;
                overflow-x: visible;
                overflow-y: auto;
                scrollbar-width: thin;
                transition: scrollbar-color 0.15s;
                &:hover {
                    scrollbar-color: #202225 transparent; }

                .category {
                    height: 18px;
                    width: calc(#{$sidebar-width} - #{$btn-side-margin} * 2 - 18px);
                    margin: 18px 8px 4px 18px;
                    border-radius: 4px;
                    box-sizing: border-box;
                    font: 12px Ginto, "Helvetica Neue", Helvetica, sans-serif;
                    font-weight: 600;
                    line-height: 16px;
                    letter-spacing: 0.25px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    display: flex;
                    span {
                        flex: 1; }
                    button.add {
                        width: 16px;
                        height: 16px;
                        margin-right: 2px; } } }

            .dm-channel {
                display: flex;
                align-items: center;
                justify-content: space-between;
                position: relative;
                width: calc(#{$sidebar-width} - #{$btn-side-margin} * 2 - 16px);
                height: $btn-height;
                margin: $btn-vertical-margin 0 $btn-vertical-margin $btn-side-margin;
                padding: 0 8px;
                border-radius: 4px; //
                text-decoration: none;
                white-space: nowrap;

                .badge {
                    margin-left: 8px;
                    flex: 0 0 auto; }

                .title {
                    display: block;
                    margin: 0;
                    font: 16px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif; //
                    font-weight: 500;
                    line-height: 20px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    flex: 1 1 auto; }

                .subtitle {
                    display: block;
                    margin: -2px 0 0 0;
                    font: 12px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif; //
                    font-weight: 500;
                    line-height: 16px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis; }

                .avatar {
                    margin-right: 12px; //
                    flex: 0 0 auto;
                    &.wrapper {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        svg {
                            width: 24px;
                            height: 24px; } } }

                .content {
                    flex: 1 1 auto;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis; }
                &:hover {
                    .close {
                        display: block; } }
                button.close {
                    width: 20px;
                    height: 20px;
                    opacity: 0.7;
                    display: none;
                    border-radius: 4px; //
                    margin-left: 8px;
                    flex: 0 0 auto;
                    svg {
                        width: 16px;
                        height: 16px;
                        margin: 2px;
 }                        // ! TODO: path doesnt conform to size
                    &:hover {
                        opacity: 1; } } } }

        .media-control {
            width: 100%;
            flex: 0 0 auto;
            z-index: 1;
            &.in-vc .vc-control {
                display: block; }

            .vc-control {
                position: relative;
                // overflow: visible
                display: none;
                padding: 8px;
                border-bottom-width: 1px;
                border-bottom-style: solid;

                .connection {
                    padding: 0 0 4px 0;
                    display: flex;
                    align-items: center;

