                            font-weight: 600;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            &.connected {
                                color: #4fdc7c; }
                            &.connecting {}

                            &.disconnected {
                                color: #ed4245; }
                            svg {
                                width: 16px;
                                height: 16px;
                                margin: 0 4px 0 0;
                                flex: 0 0 auto; } }

                        .subtitle {
                            max-width: 100%;
                            margin: 0;
                            display: block;
                            font: 12px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif; //
                            line-height: 13px;
                            text-decoration: none;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            cursor: pointer; } }

                    button.disconnect {
                        width: 32px;
                        height: 32px;
                        display: relative;
                        flex: 0 0 auto;
                        margin: 0 0 0 10px;
                        border-radius: 4px;
                        svg {
                            width: 20px;
                            height: 20px; } } }

                .actions {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin: 4px 0 0 0;

                    button {
                        width: auto;
                        min-width: auto;
                        height: 32px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex: 1 1 auto;
                        position: relative;
                        padding: 2px 16px;
                        border-radius: 3px;
                        margin: 0 4px;
                        font: 14px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif; //
