                    .title {
                        display: block;
                        margin: 0;
                        font: 14px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif; //
                        font-weight: 600;
                        line-height: 18px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis; }
                    .subtitle {
                        // height is 13
                        display: block;
                        position: relative;
                        margin: 0;
                        font: 12px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif; //
                        font-weight: 500;
                        line-height: 13px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        span {
                            display: block;
                            transition: top 0.22s ease;
 }                            // TODO: transition does not work
                        .desc {
                            position: relative; }
