            position: relative;
            display: flex;
            overflow: hidden;
            transform: translateZ(0); // t?????????

            .chat {
                flex: 1 1 auto;
                .scroller {
                    --margin-top: 8px;
                    height: calc(100% - var(--margin-top));
                    min-height: 0;
                    position: relative;
                    box-sizing: border-box;
                    padding: 0 0 8px 0;
                    margin: var(--margin-top) 0 0 0;
                    overflow: hidden scroll;
                    scrollbar-width: auto;
                    &.empty {
                        overflow: hidden; }

                    h2.title {
                        margin: 16px 20px 8px 30px;
                        font: 12px Ginto, "Helvetica Neue", Helvetica, Arial, sans-serif;
                        font-weight: 600;
                        line-height: 16px;
                        letter-spacing: 0.25px;
                        text-transform: uppercase; }

                    li.item {
                        height: 62px;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        border-top-width: 1px;
                        border-top-style: solid;
                        margin: 0 20px 0 30px;
                        box-sizing: border-box;
                        overflow: hidden;
                        cursor: pointer;
                        &:hover {
                            border-radius: 8px;
                            margin: 0 10px 0 20px;
                            padding: 0 10px;
                            // box-shadow: none
                            .user-info .name .title .tag {
                                visibility: visible; } }

                        .user-info {
                            display: flex;
                            overflow: hidden;
                            flex-shrink: 0;
                            .avatar {
                                margin: 0 12px 0 0;
                                pointer-events: none;
                                img {
                                    width: 100%;
                                    height: 100%;
                                    aspect-ratio: 1;
                                    object-fit: cover; } }
                            .name {
                                // display: flex
                                // flex-direction: column
                                .title {
                                    display: flex;
                                    align-items: flex-end;
                                    justify-content: flex-start;
                                    flex-grow: 1;
                                    margin: 0;
                                    font: 16px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif;
                                    font-weight: 600;
                                    .username {
                                        flex: 0 1 auto;
                                        overflow: hidden;
                                        white-space: nowrap;
                                        text-overflow: ellipsis; }
                                    .tag {
                                        visibility: hidden;
                                        font-size: 14px;
                                        font-weight: 500;
                                        line-height: 16px; } }

                                .subtitle {
                                    font: 14px Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif;
                                    font-weight: 500;
                                    overflow: hidden;
                                    white-space: nowrap;
                                    text-overflow: ellipsis; } } }

                        .actions {
                            display: flex;
                            margin: 0 0 0 8px;
                            button {
                                width: 36px;
                                height: 36px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                border-radius: 50%;
                                margin: 0 0 0 10px;
                                &:first-child {
                                    margin: 0; } } } } } }

            .sidebar {
                min-width: 360px;
                max-width: 420px;
                flex: 0 1 30%; }

            #active-now {}

            .tab {
                display: none;
                &.active {
                    display: block; } } } } }



.tooltip {
    max-width: 196px;
    position: absolute;
    border-radius: 5px;
    padding: 8px 12px;
    z-index: 10;
    // font: 16px
    font-weight: 600;
    line-height: 20px;
    word-wrap: break-word;
    // transform-origin: 0 50%
    .title {}

    .subtitle {}

    &::before {
        $size: 5px;
        content: '';
        width: 0;
        height: 0;
        position: absolute;
        right: 100%;
        top: 50%;
        margin-top: -$size; ////////
        border: $size solid transparent;

        border-top-color: #000; } //////// direction

    .direction {} }
